@import url(https://fonts.googleapis.com/css?family=TimesNewRoman|Inconsolata|Droid+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


   
.App {
  font-family: 'Times New Roman', Times, serif
}

:root {
  --nav-color: hsl(210, 11%, 15%);
  --nav-color-hover: hsl(210, 11%, 45%);
  --nav-border-color: hsl(210, 11%, 45%);
  --light-color: hsl(209, 100%, 92%);
  --font-color: hsl(0, 0%, 100%);
  --font-color-hover: hsl(0, 0%, 40%);
}
.navigation__container {
    background-color: var(--nav-color);
    color: white
  }
  
  .navbar-light .navbar-nav .nav-link {
    color: var(--font-color) !important;
  }
  
  .navbar-light .navbar-brand {
    color: var(--font-color) !important;
  }
  
  .navbar-light .navbar-toggler-icon {
    -webkit-filter: invert(1);
            filter: invert(1);
  }
  
  .links a:hover {
    background-color: var(--font-color-hover);
  }

  .movement {
    position:relative;
    right:50px;
    margin-right:20%;
  }
.aboutContainer {
    margin: 3rem 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    top:200px;
  }
  
  .aboutContainer img {
    border-radius: 50%;
    border: 0.5px solid rgb(218, 218, 218);
    margin-bottom: 1rem;
  }
  
  .aboutText h1 {
    text-align: center;
    color:red;
  }
  
  .aboutText p {
    text-align: center;
  }
  
  .aboutText {
    max-width: 600px;
    align-items: center;
  }
  
  
  @media only screen and (max-width: 900px) {
    .abouContainer {
      flex-direction: column;
    }
  }
  
  @media only screen and (max-width: 700px) {
    .aboutText {
      max-width: 300px;
    }
  }
.techCardsContainer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1100px;
  margin: 1rem 0;
}

#tech_h1 {
  color: white;
}

.techsContainer {
  background-color: var(--nav-color);
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}

.techCard {
  width: 15rem;
  height: 10rem;
  background-color: var(--font-color);
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  border-bottom: 5px solid var(--nav-color);
  transition: all 0.1s;
}

.techCard:hover {
  background-color: var(--font-color-hover);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.techIcons {
  height: 70px;
  /* background-color: var(--light-color); */
  /* border-radius: 50%; */
}

.contactMe__container {
    background-color: var(--nav-color);
    color: aliceblue;
    padding: 1rem;
  }
  
  .contactMe__data__links {
    display: flex;
    justify-content: space-evenly;
  }
  
  .contactMe__data__links a {
    color: white;
    text-decoration: none;
    text-transform: uppercase;
  }
  
  .contactMe__data__links a:hover {
    color: var(--light-color);
  }
  
  @media only screen and (max-width: 600px){
    .contactMe__data__links {
      flex-direction: column;
    }
  }
